<template>
    <div class="favoriteMe">
        <p class="title" >誰收藏我</p>
        <p class="sub-title">COLLECT</p>
        <el-select  v-model="value" placeholder="请选择" class="favorite-select">
            <el-option
            v-for="item in options"
            :key="item.value"
            :label="item.label"
            :value="item.value">
            </el-option>
        </el-select>
        <div class="favorite-content">
            <div class="scroll-x">
                <el-table
                    :data="tableData"
                    style="width: 100%">
                    <el-table-column
                        label="賣家資訊"
                        min-width="250"
                        >
                        <div class="favorite-user">
                            <HeadIcon :width="100"/>
                            <div class="info">
                                <p class="name">Martina Kathrine</p>
                                <p>一二三股份有限公司</p>
                                <p>連絡電話 : 09123456789</p>
                                <p>E-mail : 123456@mail</p>
                            </div>
                        </div>
                    </el-table-column>
                    <el-table-column
                        prop="data"
                        label="收藏時間"
                        width="300"
                        >
                    </el-table-column>
                    <el-table-column
                        prop="address"
                        label="收藏車款"
                        min-width="400"
                        >
                        <div class="search-result-card">
                            <div class="card-img">
                                <img src="~@/assets/img/cartype/photo-1.png" alt="" srcset="">
                            </div>
                            <div class="card-info">
                                <p class="card-title">Aston Martin Vantage</p>
                                <p class="card-txt">In a pickup market gone fancy, the Silverado sticks to its basic-truck recipe. The steering is accurate, and the Silverado handles more like a big car than a big truck.</p>
                            </div>
                        </div>
                    </el-table-column>
                </el-table>
            </div>
            <el-pagination class="pager"
                layout="prev, pager, next"
                :total="50">
            </el-pagination>
        </div>
    </div>
</template>

<script>
import HeadIcon from '../../../layout/headIcon.vue'
export default {
    name:'favoriteMe',
    components: {
        HeadIcon,

    },
    data() {
        return {
            collect: false,
            options: [{
                value: '选项1',
                label: '價錢低→高'
                }, {
                value: '选项2',
                label: '價錢高→低',
                },
            ],
            value: '',
            tableData: [
                {
                    data: '2020/10/10 AM 9:00',
                    head: '',
                    checked: true,
                }, 
                {
                    data: '2020/10/10 AM 9:00',
                    head: '',
                    checked: false,
                },
                {
                    data: '2020/10/10 AM 9:00',
                    head: '',
                    checked: false,
                },
                {
                    data: '2020/10/10 AM 9:00',
                    head: '',
                    checked: false,
                },
                {
                    data: '2020/10/10 AM 9:00',
                    head: '',
                    checked: false,
                }
                
            ],
            openParity: false,
        }
    },
    methods: { 
        setParityCar() {
            this.openParity = !this.openParity
        },
        collectCar() {
            this.collect = !this.collect
        },
    }
}
</script>

<style lang='scss' scoped>
.favoriteMe {
    padding: 20px 0;
    width: 80%;
    margin: 0 auto;
    .title {
        color: #333333;
        font-size: 40px;
        font-weight: bold;
        text-align: center;
    }
    .sub-title {
        color: #ef6d38;
        font-size: 25px;
        text-align: center;
    }
    .favorite-select {
        margin: 50px 0 20px 0;
        position: absolute;
        right: 50px;
        z-index: 1;
    }
    .favorite-content {
        padding: 120px 50px;
        position: relative;
        .favorite-user {
            display: flex;
            align-items: center;
            .head {
                border: 2px #555 solid;
                width: 100px;
                height: 100px;
                border-radius: 50px;
                text-align: center;
                i {
                    font-size: 70px;
                    line-height: 93px;
                }
            }
            .info {
                margin-left: 15px;
                .name {
                    color: #ef6d38;
                    font-weight: bold;
                    margin-bottom: 10px;
                }
            }
        }
        .search-result-card{
            display: flex;
            padding: 30px 0;
            &:nth-last-child(2) {
                border-bottom:none
            }
            .card-img {
                position: relative;
                cursor: pointer;
                img {
                    width: 150px;
                    height: 165px;
                }
            }
            .card-info {
                margin-left: 20px;
                .card-title {
                    font-size: 22px;
                    font-weight: bold;
                    margin: 30px 0 15px 0;
                    text-decoration: underline;
                }
                .card-txt {
                    max-width: 400px;
                    line-height: 25px;
                }
                .card-collect {
                    display: flex;
                    div {
                        margin: 10px;
                        padding: 0 10px;
                        transition: all .3s;
                        cursor: pointer;
                        i {
                            margin-right: 10px;
                        }
                        &:hover {
                            color: #ef6d38;
                        }
                    }
                    .card-collect-active {
                        color: #ef6d38;
                    }
                    span {
                        width: 1px;
                        display: block;
                        background: #dddddd;
                    }
                }
            }
            @media screen and (max-width: 768px){  
                align-items: center;
                flex-direction: column;
                .card-info {
                    margin: 20px 0;
                    .card-title {
                        text-align: center;
                    }
                    .card-money {
                        margin: 30px auto;
                    }
                    .card-collect {
                        margin: auto;
                        right: auto;
                    }
                }
            }
        }
    }
    .pager {
        margin: 50px 0;
    }
    @media screen and (max-width: 768px){
        width: 90%;
        .favorite-select {
            position: static;
        }
        .favorite-content {
            padding: 0;
        }
    }

    .scroll-x{
        max-width: calc(100vw - 575px);
        overflow-x: auto;
    }
    @media screen and (max-width: 768px){ 

        .scroll-x{
            overflow: visible;
            max-width: none;
        }
        
    }
}

</style>


